}
div.grid {
        display: grid;
        gap: 2px;
	grid-auto-columns: 9px;
	grid-auto-rows: 9px;
}
button.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0;
}

